import React, { useEffect, useState } from 'react';
import TablBoats from '../../DataBase.js/TablBoats';
import { Link, useNavigate } from 'react-router-dom';

const Discover = () => {

    const [item, setItem] = useState(TablBoats)
    const navigate = useNavigate();


    const handleLinkClick = () => {
        document.body.classList.add('fade-out');
        setTimeout(() => {
            window.location.reload();
        }, 500);
        setTimeout(() => {
            window.scrollTo({ top: 0, behavior: 'auto' });
        }, 500);
}

const [randomItems, setRandomItems] = useState([]);
useEffect(() => {
    const shuffleWithDelay = (items, delay) => {
        setTimeout(() => {
            const shuffledItems = items.sort(() => Math.random() - 0.5);
            setRandomItems(shuffledItems.slice(0, 3));
        }, delay);
    };
    shuffleWithDelay(item, 100); // Shuffle the items after a delay of 1000 milliseconds (1 second)
}, [item]);

// const shuffledItems = item.sort(() => Math.random() - 0.5); // Shuffle the items
// const randomItems = shuffledItems.slice(0, 3); // Take the first three items
return (
    <div style={{ display: 'flex', flexWrap: 'nowrap', scale: '0.8', alignItems: 'center', justifyContent: 'center' }}>
        {randomItems.map((item) => (
            <div>
                <div style={{ display: 'flex', justifyContent: 'space-evenly', marginTop: '0', alignItems: 'center', marginBottom: '10%', flexDirection: 'column', gap: '40px' }}>
                    <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <img src={item.imgBoatOne} style={{ height: '400px', width: '600px', backgroundColor: '#F6F5F6' }} />
                    </div>
                    <div style={{ width: '80%', display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column', gap: '40px', scale: '1' }}>
                        <div style={{ width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'start', gap: '20px' }}>
                            <h2 style={{ fontFamily: 'sweet-sans-pro', color: '#D3414A', textTransform: 'uppercase', fontSize: '26px', fontWeight: '600', margin: '0' }} >
                                {item.titleBoat}
                            </h2>
                            <p style={{ fontFamily: 'sweet-sans-pro', color: 'black', fontWeight: '400', width: '100%', margin: '0', height: '170px', textAlign: 'justify' }}>
                                {item.desciptionBoat}
                            </p>
                        </div>
                        <Link className='button' to='/Opiom/DescriptifTypes' state={{ Item: item }} onClick={handleLinkClick}>
                            EN SAVOIR PLUS
                        </Link>
                    </div>
                </div>
            </div>
        ))}
    </div>
);
}

export default Discover;