import React from 'react';
import facebook from '../../assets/facebook.svg'
import instagram from '../../assets/instagram.svg'
import linkedin from '../../assets/linkedin.svg'
import phone from '../../assets/phone-alt.svg'
import boat from '../../assets/icon-boat.svg'
import courrier from '../../assets/icon-courriet.svg'

const NewsOpiom = () => {
    return (
        <div style={{marginBottom:'0'}}>
            <div style={{height:'5vh', backgroundColor:'black', display:'flex', alignItems:'center', justifyContent:'space-around'}}>
                <div style={{display:'flex', alignItems:'center', gap:'20px'}}>
                    <p style={{color:'white',fontWeight:'bold',fontFamily:'sweet-sans-pro'}}>Retrouvez-nous sur</p>
                    <a href='#'><img src={facebook}  style={{height:'20px', width:'20px', marginBottom:'-5px'}}/></a>
                    <a href='#'><img src={instagram} style={{height:'20px', width:'20px', marginBottom:'-5px'}}/></a>
                    <a href='#'><img src={linkedin} style={{height:'20px', width:'20px', marginBottom:'-5px'}}/></a>
                </div>
                <div style={{display:'flex', alignItems:'center', gap:'50px'}}>
                    <div style={{display:'flex', alignItems:'center', gap:'20px'}}>
                        <img src={phone} style={{height:'24px', width:'24px'}}/>
                        <a href='tel:06 06 06 06 06' style={{color:'white', textDecoration:'none',fontWeight:'bold',fontFamily:'sweet-sans-pro'}}>06 06 06 06 06</a>
                    </div>
                    <div style={{display:'flex', alignItems:'center', gap:'20px'}}>
                        <img src={boat} style={{height:'24px', width:'24px'}}/>
                        <a href='#' style={{color:'white',fontWeight:'bold',fontFamily:'sweet-sans-pro',textDecoration:'none'}}>Réservez votre événement</a>
                    </div>
                    <div style={{display:'flex', alignItems:'center', gap:'20px'}}>
                        <img src={courrier} style={{height:'24px', width:'24px'}}/>
                        <a href='/Opiom/Contact' style={{color:'white',fontWeight:'bold',fontFamily:'sweet-sans-pro',textDecoration:'none'}}>Contactez-nous</a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default NewsOpiom;