import React from 'react';
import logo from '../../assets/LOGO_WHITE.png'
import { useLocation } from 'react-router';

const FooterOpiom = () => {
        const routeLocation = useLocation();
    
      
        const footerStyle =
          routeLocation.pathname === "/Opiom/Contact"
            ? { color: "#D3414A", fontWeight:'700' }
            : { color: "white" };
    

        
    return (
        <div>
            <div style={{display:'flex', justifyContent:'space-evenly', width:'100%', height:'10vh', backgroundColor:'black', alignItems:'center'}}>
                <div>
                    <img src={logo} style={{height:'30px'}}/>
                </div>
                <div style={{display:'flex', gap:'80px'}}>
                    <a href='/Opiom/Contact' style={{fontFamily:'sweet-sans-pro', fontWeight:'400', fontSize:'18px', ...footerStyle}}>Contactez-nous</a>
                    <a href='#' style={{fontFamily:'sweet-sans-pro', fontWeight:'400', color:'white', fontSize:'18px'}}>Mentions légales</a>
                    <a href='#' style={{fontFamily:'sweet-sans-pro', fontWeight:'400', color:'white', fontSize:'18px'}}>Politique de confidentialité</a>
                </div>
                <div>
                    <label style={{fontFamily:'sweet-sans-pro', fontWeight:'800', fontSize:'18px', color:'white'}}>Agence Backstages - 2023</label>
                </div>
            </div>
        </div>
    );
};

export default FooterOpiom;