import React from 'react';
import logo from '../../assets/logo_true.svg'
import account from '../../assets/favicon.svg'
import account_2 from '../../assets/favicon_2.svg'
import '../../styles/HeaderOpiom.css'
import { useLocation } from 'react-router';

const HeaderOpiom = () => {

    const headerLocation = useLocation();


    const footerStyle =
        headerLocation.pathname === "/Opiom/Contact"
            ? { color: "white", fontWeight: '600' }
            : { color: "black" };

    const footerImg =
        headerLocation.pathname === "/Opiom/Contact"
            ? account_2
            : account;

    return (
        <div className='header'>
            <div>
                <a href='/Opiom/Accueil'>
                    <img style={{ height: '30px' }} src={logo} />
                </a>
            </div>
            <div style={{ display: 'flex', alignContent: 'center' }}>
                <ul style={{ display: 'flex', alignItems: 'center', gap: '60px', }}>
                    <a href='/Opiom/Accueil' style={{ ...footerStyle }}>Accueil</a>
                    <a href='/Opiom/Nos_Bateaux' style={{ ...footerStyle }}>Nos bateaux</a>
                    <a href='/Opiom/Nos_Prestation' style={{ ...footerStyle }}>Nos prestations</a>
                    <a href='/Opiom/Notre_Histoire' style={{ ...footerStyle }}>Notre histoire</a>
                    <a href='#' style={{ display: 'flex', alignItems: 'center', gap: '15px', ...footerStyle }}><img src={footerImg} style={{ height: '25px' }} />Mon espace</a>
                </ul>
            </div>
        </div>
    );
};

export default HeaderOpiom;