import React, { useState } from 'react';
import back from '../assets/back_2.jpg'
import photo from '../assets/placeholderImage.png'
import TablBoats from '../DataBase.js/TablBoats';
import BoatCard from '../components/Boats/BoatCard';

const OurBoats = () => {

    return (
        <div>
            <div style={{ height: '95vh', backgroundImage: `url(${back})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: '25% 75%', zIndex:'1', marginTop:'-10vh' }}>
                <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'end', width: '92%', height: '95vh' }}>
                    <div style={{ backgroundColor: 'white', height: '330px', width: '600px', display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexDirection: 'column', gap: '0px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', height: '80%', gap: '0px' }}>
                            <h1 style={{ fontFamily: 'DM Serif Display', fontSize: '30px', fontStyle: 'normal', fontWeight: '400' }}>
                                Les bateaux Opiom
                            </h1>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '10px' }}>
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '16px', fontWeight: '400', width: '73%', textAlign: 'justify' }}>
                                    Aquitana, Burdigala I ou encore Burdigala II,
                                    découvrez les 3 bateaux disponibles pour l'organisation de vos
                                    évènements privés ou professionnels.
                                </p>
                                <a href='#' className='button'>
                                    DÉCOUVREZ-NOUS
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <h4 style={{marginTop:'5%', fontFamily: 'sweet-sans-pro', textTransform: 'uppercase', color: '#262626', fontWeight: '700', fontSize: '26px', width: '40%', textAlign: 'center' }}>
                DÉCOUVREZ NOS OFFRES
            </h4>
            <div style={{ display: 'flex', justifyContent: 'space-around', marginTop: '4%', marginBottom: '5%', scale: '1' }}>
                <div style={{ display: 'flex', flexDirection: 'column', width: '30%', alignItems: 'center', gap: '40px', scale: '1' }}>
                    <img src={photo} style={{ height: '250px', width: '400px', backgroundColor: '#F6F5F6' }} />
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', alignItems: 'center' }}>
                        <p style={{ width: '100%', textTransform: 'uppercase', color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '700', fontSize: '20px', margin: '0', textAlign: 'center' }}>
                            VUE IMPRENABLE SUR BORDEAUX
                        </p>
                        <p style={{ color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '400', fontSize: '16px', textAlign: 'left', width: '85%', margin: '0', height: '150px' }}>
                            En montant à bord d’un bateau Opiom,
                            vous profiterez d’une <b>vue imprenable sur Bordeaux</b> depuis la Garonne.
                            Vous pourrez ainsi découvrir les magnifiques façades qui composent la ville le long
                            de la Garonne lors de ce moment exceptionnel qu’est une <b style={{ color: 'red' }}>croisière Opiom</b>.
                        </p>
                    </div>
                    <a href='#' style={{ width: 'auto', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '16px', paddingLeft: '30px', paddingRight: '30px', paddingTop: '15px', paddingBottom: '15px' }}>
                        DEMANDER VOTRE DEVIS
                    </a>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', width: '30%', alignItems: 'center', gap: '40px', scale: '1' }}>
                    <img src={photo} style={{ height: '250px', width: '400px', backgroundColor: '#F6F5F6' }} />
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', alignItems: 'center' }}>
                        <p style={{ width: '100%', textTransform: 'uppercase', color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '700', fontSize: '20px', margin: '0', textAlign: 'center' }}>
                            UN SERVICE SUR-MESURE UNIQUE
                        </p>
                        <p style={{ color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '400', fontSize: '16px', textAlign: 'left', width: '85%', margin: '0', height: '150px' }}>
                            Opiom vous propose une <b style={{ color: '#D3414A' }}>personnalisation complète de vos événements</b>,
                            du choix du bateau au choix des menus proposés à bord.
                            Votre événement est unique, notre prestation le sera tout autant.
                            <b> L’expertise et le professionnalisme d’Opiom</b> saura faire la différence et rendre votre événement inoubliable.
                        </p>
                    </div>
                    <a href='#' style={{ width: 'auto', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '16px', paddingLeft: '30px', paddingRight: '30px', paddingTop: '15px', paddingBottom: '15px' }}>
                        DEMANDER VOTRE DEVIS
                    </a>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', width: '30%', alignItems: 'center', gap: '40px', scale: '1' }}>
                    <img src={photo} style={{ height: '250px', width: '400px', backgroundColor: '#F6F5F6' }} />
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', alignItems: 'center' }}>
                        <p style={{ width: '100%', textTransform: 'uppercase', color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '700', fontSize: '20px', margin: '0', textAlign: 'center' }}>
                            UNE ÉQUIPE DÉDIÉE POUR VOUS
                        </p>
                        <p style={{ color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '400', fontSize: '16px', textAlign: 'left', width: '85%', margin: '0', height: '150px' }}>
                            Lorsque vous montez à bord d’un bateau Opiom,
                            vous bénéficiez d’un <b style={{ color: '#D3414A' }}>accompagnement sur-mesure et personnalisé</b> tout au long de l’organisation de votre événement.
                            Nous faisons du <b>bien-être</b> de vos convives notre priorité.
                        </p>
                    </div>
                    <a href='#' style={{ width: 'auto', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '16px', paddingLeft: '30px', paddingRight: '30px', paddingTop: '15px', paddingBottom: '15px' }}>
                        DEMANDER VOTRE DEVIS
                    </a>
                </div>
            </div>
            <div style={{ marginBottom: '5%' }}>
                <div style={{ height: '100px', width: '100%', backgroundColor: '#F6F5F6', display: 'flex', alignItems: 'center' }}>
                    <p style={{ fontFamily: 'sweet-sans-pro', fontWeight: '700', color: '#D3414A', fontSize: '26px', textAlign: 'center', width: '25%' }}>
                        RECHERCHER
                    </p>
                </div>
            </div>
            <h4 style={{ marginBottom: '3%', fontFamily: 'sweet-sans-pro', textTransform: 'uppercase', color: '#262626', fontWeight: '700', fontSize: '26px', width: '40%', textAlign: 'center' }}>
                DÉCOUVREZ NOS BATEAUX
            </h4>
            {TablBoats.map((item, index) => index % 2 === 1 ? <BoatCard isLeft={true} key={index} boat={item} /> : <BoatCard isLeft={false} key={index} boat={item} />)}
        </div>
    );
};

export default OurBoats;