const event = 
    {
        photo: photo,
    }


const CarouselOpiom = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2500,
    // prevArrow: <CustomPrevArrow />,
    // nextArrow: <CustomNextArrow />
  };
  
  return (
    <div style={{margin: '0 auto', width:'100%'}}>
        <Slider {...settings}>
        <div>
            <a href='#'>
                <img style={{margin: '0 auto', display:'block', backgroundColor:'#E4E4E4', width:'400px', height:'400px'}} src={photo} alt="event 1" />
            </a>
        </div>
        <div>
            <a href='#'>
                <img style={{margin: '0 auto', display:'block', backgroundColor:'#E4E4E4', width:'400px', height:'400px'}} src={photo} alt="event 2" />
            </a>
        </div>
        <div>
            <a href='#'>
                <img style={{margin: '0 auto', display:'block', backgroundColor:'#E4E4E4', width:'400px', height:'400px'}} src={photo} alt="event 3" />
            </a>
        </div>
        <div>
            <a href='#'>
                <img style={{margin: '0 auto', display:'block', backgroundColor:'#E4E4E4', width:'400px', height:'400px'}} src={photo} alt="event 4" />
            </a>
        </div>
        </Slider>
    </div>
  );
};

export default CarouselOpiom;