import React, { useState } from 'react';
import back from '../assets/back_2.jpg'
import { useLocation } from 'react-router';
import Discover from '../components/Boats/Discover';
import CarousselEvent from '../components/Carousel/CarousselEvent';

const DescriptifTypes = () => {

    let { state } = useLocation()
    const [Item, setItem] = useState(state ? state.Item : null);
    console.log(state)


    return (
        <div>
            <div style={{ height: '95vh', backgroundImage: `url(${back})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: '25% 75%', zIndex: '1', marginTop: '-10vh' }}>
                <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'end', width: '94%', height: '95vh' }}>
                    <div style={{ backgroundColor: 'white', height: '330px', width: '600px', display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexDirection: 'column', gap: '0px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', height: '90%', gap: '10px' }}>
                            <h1 style={{ fontFamily: 'DM Serif Display', fontSize: '30px', fontStyle: 'normal', fontWeight: '400', marginBottom: '0', textTransform: 'uppercase' }}>
                                {Item && Item.titleBoat}
                            </h1>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '10px' }}>
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '16px', fontWeight: '400', width: '73%', textAlign: 'justify' }}>
                                    {Item && Item.desciptionBoat}
                                </p>
                                {/* <a href='#' style={{fontSize:'16px'}} className='button'>
                                    RÉSERVER
                                </a> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'space-around', marginTop: '5%', marginBottom: '2%', scale: '1' }}>
                <div>
                    <img src={Item && Item.imgBoatOne} style={{ backgroundColor: '#E4E4E4', height: '500px', width: '520px' }} />
                </div>
                <div>
                    <img src={Item && Item.imgBoatTwo} style={{ backgroundColor: '#E4E4E4', height: '500px', width: '520px' }} />
                </div>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', gap: '20px', marginBottom: '5%' }}>
                <div style={{ width: '50%', marginLeft: '5%' }}>
                    <div>
                        <h2 style={{ textTransform: 'uppercase', color: '#262626', fontFamily: 'sweet-sans-pro', }}>
                            {Item && Item.titleBoat}, UNE EXPÉRIENCE UNIQUE
                        </h2>
                    </div>
                    <p style={{ color: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '400', fontSize: '16px', textAlign: 'left', width: '85%', margin: '0', height: 'auto' }}>
                        {Item && Item.sousDescriptionBoat}
                    </p>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', width: '38%', alignItems: 'center', gap: '4vh' }}>
                    <p style={{ textTransform: 'uppercase', color: '#D3414A', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '26px' }}>
                        À PARTIR DE {Item && Item.price} € (HT)
                    </p>
                    <a href='#' style={{ width: 'auto', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '20px', paddingLeft: '30px', paddingRight: '30px', paddingTop: '15px', paddingBottom: '15px' }}>
                        RÉSERVER MAINTENANT
                    </a>
                </div>
            </div>
            <div>
            <div style={{ marginBottom: '1%' }}>
                    <h4 style={{ fontFamily: 'sweet-sans-pro', color: '#262626', textTransform: 'uppercase', fontSize: '26px', fontWeight: '700', margin: '0', textAlign: 'center', width: '30%' }} >
                        LES DERNIERS ÉVÉNEMENTS
                    </h4>
                </div>
                <div className='carrouselOverflow' style={{ marginBottom: '7%', scale: '0.8', width: '100%' }}>
                    <CarousselEvent />
                </div>
                <div style={{ marginBottom: '1%' }}>
                    <h4 style={{ fontFamily: 'sweet-sans-pro', color: '#262626', textTransform: 'uppercase', fontSize: '26px', fontWeight: '700', margin: '0', textAlign: 'center', width: '30%' }} >
                        DÉCOUVREZ NOS BATEAUX
                    </h4>
                </div>
                <div>
                    <Discover />
                </div>
            </div>
        </div>
    );
};

export default DescriptifTypes;