import './App.css';
import HomePage from './page/HomePage';
import './styles/Polices.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import OurBoats from './page/OurBoats';
import NosPrestations from './page/NosPrestations';
import OurStory from './page/OurStory';
import ContactUs from './page/ContactUs';
import Wrapper from './components/Wrapper';
import DescriptifTypes from './page/DescriptifTypes';
import ERROR404 from './page/ERROR404';
import Cocktail from './page/Cocktail';
import BurgerMenu from './components/Wrapper/BurgerMenu';

function App() {
  return (
    <BrowserRouter>
    <Wrapper>
      <Routes>
        <Route path='/Opiom/Accueil' element={<HomePage />}></Route>
        <Route path='/Opiom/Nos_Bateaux' element={<OurBoats  />}></Route>
        <Route path='/Opiom/Nos_Prestation' element={<NosPrestations  />}></Route>
        <Route path='/Opiom/Notre_Histoire' element={<OurStory  />}></Route>
        <Route path='/Opiom/Contact' element={<ContactUs  />}></Route>
        <Route path='/Opiom/DescriptifTypes' element={<DescriptifTypes  />}></Route>
        <Route path='/Opiom/Cocktail' element={<Cocktail  />}></Route>
        <Route path='/Opiom/test' element={<BurgerMenu  />}></Route>
        <Route path='*' element={<ERROR404  />}></Route>
      </Routes>
     </Wrapper> 
    </BrowserRouter>
  );
}

export default App;
