import React, { Children } from 'react';
import HeaderOpiom from './HeaderOpiom';
import FooterOpiom from './FooterOpiom';
import NewsOpiom from './NewsOpiom';
import BurgerMenu from './BurgerMenu';

const index = ({children}) => {
    return (
        <>
            <NewsOpiom />
            <HeaderOpiom />
            {children}
            <FooterOpiom />
        </>
    );
};

export default index;