import React from 'react';

const ERROR404 = () => {
    return (
        <div style={{height:'70.1vh', display:'flex', alignItems:'center', flexDirection:'column', justifyContent:'center', gap:'3vh'}}>
            <h1 style={{textTransform:'uppercase' ,margin:'0',fontFamily: 'DM Serif Display', fontSize: '40px', fontStyle: 'normal', fontWeight: '400' }}>
                ERROR 404
            </h1>
            <h2 style={{textTransform:'uppercase', margin:'0',fontFamily: 'DM Serif Display', fontSize: '40px', fontStyle: 'normal', fontWeight: '400' }}>
                Page introuvable
            </h2>
            <a href='/Opiom/Accueil' style={{textTransform:'uppercase'}} className='button' >
                Revenir à l'accueil
            </a>
        </div>
    );
};

export default ERROR404;