import React from 'react';
import { Link } from 'react-router-dom';

const BoatCard = ({ isLeft, boat }) => {

        const handleLinkClick = () => {
            window.scrollTo({ top: 0, behavior: 'auto' })
        };

    return (
        <section>
            <div style={{flexDirection: isLeft ? 'row-reverse' : 'row', display: 'flex', justifyContent: 'space-evenly', marginTop: '0', alignItems: 'center', marginBottom: '10%', scale:'0.9' }}>
                <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    <img src={boat.imgBoatOne} style={{ height: '400px', width: '700px', backgroundColor: '#F6F5F6' }} />
                </div>
                <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column', gap: '40px', scale: '1.1' }}>
                    <div style={{ width: '82%', display: 'flex', flexDirection: 'column', alignItems: 'start', gap: '20px' }}>
                        <h2 style={{ fontFamily: 'sweet-sans-pro', color: '#D3414A', textTransform: 'uppercase', fontSize: '26px', fontWeight: '600', margin: '0' }} >
                            {boat.titleBoat}                                 </h2>
                        <p style={{ fontFamily: 'sweet-sans-pro', color: 'black', fontWeight: '400', width: '100%', margin: '0', lineHeight: '1.6em' }}>
                            {boat.desciptionBoat}
                        </p>
                    </div>
                    <Link className='button' to='/Opiom/DescriptifTypes' state={{ Item: boat }} onClick={handleLinkClick}>
                        EN SAVOIR PLUS
                    </Link>
                </div>
            </div>
        </section>
    );
};

export default BoatCard;