import React, { useState } from 'react';
import back from '../assets/back_3.jpg';
import photo from '../assets/placeholderImage.png'
import CarousselPrestataires from '../components/Carousel/CarousselPrestataires';
import BoatCard from '../components/Boats/BoatCard';
import TablBoats from '../DataBase.js/TablBoats';

const NosPrestations = () => {

    return (
        <div>
            <div style={{ height: '95vh', backgroundImage: `url(${back})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: '25% 75%', zIndex:'1', marginTop:'-10vh' }}>
                <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'end', width: '92%', height: '95vh', marginBottom: '10%' }}>
                    <div style={{ backgroundColor: 'white', height: '330px', width: '600px', display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexDirection: 'column', gap: '0px' }}>
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', height: '80%', gap: '0px' }}>
                            <h1 style={{ fontFamily: 'DM Serif Display', fontSize: '30px', fontStyle: 'normal', fontWeight: '400' }}>
                                L’univers d’Opiom
                            </h1>
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '1px' }}>
                                <p style={{ fontFamily: 'sweet-sans-pro', fontSize: '16px', fontWeight: '400', width: '73%', textAlign: 'justify' }}>
                                    Vous êtes à la recherche d’une agence événementielle pour organiser vos
                                    événements à bord de somptueux bateau sur la Garonne ?
                                    Opiom se met à votre service et vous propose des <b>événements sur-mesure et 100% personnalisé</b>.
                                </p>
                                <a href='#' className='button'>
                                    DÉCOUVREZ-NOUS
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', marginBottom: '10%',marginTop: '10%' }}>
                <div style={{ display: 'flex', flexDirection: 'column', width: '30%', justifyContent: 'center', gap: '30px' }}>
                    <h2 style={{ fontFamily: 'sweet-sans-pro', fontSize: '26px', textTransform: 'uppercase', margin: '0' }}>
                        UNE OFFRE SUR-MESURE
                    </h2>
                    <p style={{ fontFamily: 'sweet-sans-pro', margin: '0', fontWeight: '400', fontSize: '16px' }}>
                        L’offre d’Opiom est 100% personnalisable et s’adapte à tous vos besoins.
                        Que ce soit pour un <b style={{ color: '#D3414A' }}>événement privé</b>, comme un <b>mariage</b>, un <b>anniversaire</b>,
                        un <b>baptême</b>, un <b>EVG</b> ou <b>EVJF</b> (enterrement de vie de garçon ou enterrement de vie de jeune fille),
                        ou bien pour un <b style={{ color: '#D3414A' }}>événement professionnel</b> comme un <b>séminaire</b>, un <b>pot de départ</b> ou d’arrivée,
                        un <b>départ à la retraite</b>, une <b>soirée de lancement</b> de produit, un <b>arbre de Noël</b> ou un <b>gala d’entreprise</b>,
                        Opiom est à vos côtés pour l’organisation sur-mesure.
                    </p>
                    <a href='#' style={{ width: '20%', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '18px', paddingLeft: '60px', paddingRight: '60px', paddingTop: '15px', paddingBottom: '15px', textAlign: 'center', }}>
                        BOUTON
                    </a>
                </div>
                <div>
                    <img src={photo} style={{ backgroundColor: '#E4E4E4', width: '900px', height: '350px', backgroundSize: 'cover' }} />
                </div>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', marginBottom: '10%' }}>
                <div>
                    <img src={photo} style={{ backgroundColor: '#E4E4E4', width: '500px', height: '400px' }} />
                </div>
                <div style={{ display: 'flex', flexDirection: 'column', width: '50%', justifyContent: 'center', gap: '30px', textAlign: 'right' }}>
                    <h3 style={{ fontFamily: 'sweet-sans-pro', fontSize: '26px', textTransform: 'uppercase', margin: '0' }}>
                        UNE OFFRE SUR-MESURE
                    </h3>
                    <p style={{ fontFamily: 'sweet-sans-pro', margin: '0', fontWeight: '400', fontSize: '16px' }}>
                        La force d’Opiom réside dans son soin du détail et son <b>offre hautement personnalisable</b>.
                        Toute l’équipe accorde une grande importance à votre satisfaction.
                        C’est pourquoi nous vous proposons une <b style={{ color: '#D3414A' }}>offre sur-mesure</b> accompagnée par nos prestataires triés sur le volet.
                        Ensemble, nous mettons tout en oeuvre pour vous satisfaire.
                    </p>
                    <p style={{ fontFamily: 'sweet-sans-pro', margin: '0', fontWeight: '400', fontSize: '16px' }}>
                        Pour vous offrir la plus belle des expériences à bord,
                        nous vous proposons donc de personnaliser l’intégralité des prestations disponibles sur le bateau.
                        Vous pouvez personnaliser vos <b style={{ color: '#D3414A' }}>prestations food, bar et animation</b> selon vos envies et les moduler.
                        En faisant appel à Opiom pour l’organisation de votre événement,
                        vous optez pour une <b>prestation hautement qualitative</b>,
                        qui comblera l’ensemble de vos passagers lors de vos événements.
                    </p>
                    <a href='#' style={{ width: '20%', color: '#ffffff', backgroundColor: 'black', fontFamily: 'sweet-sans-pro', fontWeight: '600', fontSize: '18px', paddingLeft: '60px', paddingRight: '60px', paddingTop: '15px', paddingBottom: '15px', textAlign: 'center', alignSelf: 'end' }}>
                        BOUTON
                    </a>
                </div>
            </div>
            <div style={{ marginBottom: '5%' }}>
                <h4 style={{ fontFamily: 'sweet-sans-pro', color: '#262626', textTransform: 'uppercase', fontSize: '26px', fontWeight: '700', margin: '0', textAlign: 'center', width: '30%' }} >
                    NOS PRESTATAIRES
                </h4>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', gap: '50px', width: '100%', float: 'right', marginBottom: '10%', scale: '0.8' }}>
                <CarousselPrestataires />
            </div>
            <div style={{ marginBottom: '1%' }}>
                <h4 style={{ fontFamily: 'sweet-sans-pro', color: '#262626', textTransform: 'uppercase', fontSize: '26px', fontWeight: '700', margin: '0', textAlign: 'center', width: '37%' }} >
                    DÉCOUVREZ NOS BATEAUX
                </h4>
            </div>
            {TablBoats.map((item, index) => index % 2 === 1 ? <BoatCard isLeft={true} key={index} boat={item} /> : <BoatCard isLeft={false} key={index} boat={item} />)}
        </div>
    );
};

export default NosPrestations;